'use client';

import React, { useState, FormEvent } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import headerImage from '../../../../public/assets/Logo Icon.png';
import sideImage from '../../../../public/assets/resetPasswrodIllustration.png';
import Image from 'next/image';
import Link from 'next/link';
import '../../../../src/app/globals.css';
import Eye from '../../../../public/assets/Eye.png';
import EyeClosed from '../../../../public/assets/Property 1=EyeClosed.png';
import Toast from '../../../components/auth/Toast'
// import { useParams } from 'next/navigation';
import axios from 'axios';
// import { useRouter } from 'next/router';
import { useRouter } from 'next/router';

export default function ResetPassword() {
  const router = useRouter();
  const { id } = router.query;
  console.log(id);
  // const router = useRouter();
  // const { id } = router.query;
  const [visibility, setVisibility] = useState(true);
  const [formData, setFormData] = useState({
    password: '',
    confirmPassword: '',
  });
  const [passwordValidation, setPasswordValidation] = useState({
    hasUpperCase: false,
    hasLowerCase: false,
    hasNumber: false,
    hasSpecialChar: false,
    isLongEnough: false,
  });
  const [showValidation, setShowValidation] = useState(false);

  const validatePassword = (password: string) => {
    setPasswordValidation({
      hasUpperCase: /[A-Z]/.test(password),
      hasLowerCase: /[a-z]/.test(password),
      hasNumber: /\d/.test(password),
      hasSpecialChar: /[!@#$%^&*(),.?":{}|<>]/.test(password),
      isLongEnough: password.length >= 12,
    });
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));

    if (name === 'password') {
      setShowValidation(true);
      validatePassword(value);
    }
  };

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    if (!formData.password) {
      toast.error('Password is required');
      return;
    }
    if (!formData.confirmPassword) {
      toast.error('Confirm Password is required');
      return;
    }
    if (formData.password !== formData.confirmPassword) {
      toast.error('Passwords do not match');
      return;
    }

    const { isLongEnough, hasUpperCase, hasLowerCase, hasNumber, hasSpecialChar } = passwordValidation;
    if (!isLongEnough || !hasUpperCase || !hasLowerCase || !hasNumber || !hasSpecialChar) {
      toast.error('Please fulfill all password requirements');
      return;
    }

    try {
      const response = await axios.post(`${process.env.NEXT_PUBLIC_API_URL}/auth/reset-password?id=${userId}`, {
        password: formData.password,
        confirmPassword: formData.confirmPassword,
      });
  
      if (response.status === 200) {
        toast.success('Password reset successfully!');
      } else {
        toast.error(response.data.message || 'Something went wrong');
      }
    } catch (error) {
      toast.error(error.response?.data?.message || 'Failed to reset password');
    }

    // toast.success('Password reset successfully!');
    console.log('Form Data Submitted:', formData);
  };

  return (
    <>
    <Toast/>
    <div className="flex h-screen">
      <div className="flex-1 p-8 flex flex-col justify-center items-center bg-white">
        <div className="flex items-center mb-6">
          <Image src={headerImage} alt="Logo" className="w-12 h-auto" />
          <span className="ml-3 text-2xl font-sans font-bold text-black">
            BlackcofferToDos
          </span>
        </div>

        <h1 className="text-3xl font-bold mb-2 text-center font-sans">Create New Password</h1>
        <p className="text-lg mb-8 text-gray-700 text-center font-sans">Enter a new password</p>

        <form className="w-full max-w-sm" onSubmit={handleSubmit}>
          <label className="block mb-4">
            <span className="text-black font-sans font-semibold">New Password</span>
            <div className="relative">
              <input
                type={visibility ? 'password' : 'text'}
                name="password"
                value={formData.password}
                onChange={handleChange}
                className="mt-2 block w-full rounded-md border border-gray-300 shadow-sm p-3 focus:ring-indigo-500 focus:border-indigo-500"
                placeholder="Create new password"
                aria-label="New Password"
              />
              <button
                type="button"
                onClick={() => setVisibility(!visibility)}
                className="absolute right-3 top-3 text-gray-500 hover:text-gray-700"
              >
                <Image src={visibility ? EyeClosed : Eye} alt="Toggle Visibility" />
              </button>
            </div>
            {/* <span className="text-sm text-gray-500 mt-1 block">Passwords must be at least 12 characters long.</span> */}
          </label>

         
          {showValidation && (
            <ul className="mb-4 text-sm">
              <li className={passwordValidation.isLongEnough ?'hidden' : 'text-red-600'}>
                Must contain at least 12 characters
              </li>
              <li className={passwordValidation.hasUpperCase ? 'hidden' : 'text-red-600'}>
                Must contain at least one uppercase letter
              </li>
              <li className={passwordValidation.hasLowerCase ? 'hidden' : 'text-red-600'}>
                Must contain at least one lowercase letter
              </li>
              <li className={passwordValidation.hasNumber ? 'hidden' : 'text-red-600'}>
                Must contain at least one number
              </li>
              <li className={passwordValidation.hasSpecialChar ? 'hidden' : 'text-red-600'}>
                Must contain at least one special character
              </li>
            </ul>
          )}

          <label className="block mb-6">
            <span className="text-black font-sans font-semibold">Confirm Password</span>
            <input
              type= 'password'
            //   {visibility ? 'text' : 'password'}
              name="confirmPassword"
              value={formData.confirmPassword}
              onChange={handleChange}
              className="mt-2 block w-full rounded-md border border-gray-300 shadow-sm p-3 focus:ring-indigo-500 focus:border-indigo-500"
              placeholder="Re-enter password"
              aria-label="Confirm Password"
            />
          </label>

          <button
            type="submit"
            className="w-full bg-indigo-500 text-white py-3 rounded-md hover:bg-indigo-600 font-medium"
          >
            Confirm
          </button>
        </form>

        <p className="mt-6 text-gray-600 text-center font-sans">
          Go back to{' '}
          <Link href="/password/Auth/login" className="text-indigo-500 font-medium hover:underline">
            Log In
          </Link>
        </p>
      </div>

      {/* Right Section */}
      <div className="flex-1 bg-indigo-50 flex justify-center items-center">
        <Image priority src={sideImage} alt="Illustration" className="w-3/4 max-w-md" />
      </div>
    </div>
    </>
  );
}
